<template>
    <div class="row">
      <div class="col-xs-12 col-md-8">
        <div> <!--general-->
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
        <div class="card">
          <div class="card-header">
            <strong>Draft Budget Plan</strong>
          </div>            
          <div class="card-body card-block">
            <div class="row form-group">
                <div class="col col-md-3"><label class="form-control-label">Department</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <select name="Request_for" id="rf" class="form-control select" v-model="budget.dept" @change="load_supdept">
                        <option v-for="dep in dept_list">{{ dep.department }}</option>
                    </select>
                </div>
            </div>
            <div class="row form-group" v-if="display_sub">
                <div class="col col-md-3"><label class="form-control-label">Sub-Department</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <select name="Request_for" id="rf" class="form-control select" v-model="budget.sub">
                        <option v-for="sub_dep in sub_dept_list">{{ sub_dep.subdept_full }}</option>
                    </select>
                </div>
            </div>
            <div class="row form-group">
                <div class="col col-md-3"><label class="form-control-label">Asset Category</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <select name="Request_for" id="rf" class="form-control select" v-model="budget.cat">
                        <option v-for="cat in asset_list">{{ cat.name }}</option>
                    </select>
                </div>
            </div>
            <div class="row form-group">
                <div class="col col-md-3"><label class="form-control-label">Type of Funding</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <select name="Request_for" id="rf" class="form-control select" v-model="budget.type">
                        <option v-for="bt in type_list">{{ bt }}</option>
                    </select>
                </div>
            </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Description</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <textarea id ="Description" name="Description" rows="2" cols="80" placeholder="Enter description" maxlength="250" class="form-control" required v-model="budget.descript"></textarea>                   
                </div>
              </div>  
              <div style='display:flex;flex-direction:row;width:100%'>
                <div class="row">
                        <label class=" form-control-label" style="padding-top:0.2em;margin-left:1em">Unit Cost :</label>
                        <input type='number' step='0.001' class="form-control" style="width:10em;margin-left:1em" v-model="unit_cost" @change="cal_num" required>                   
                </div>
                <div class="row" style="margin-left:3em">
                        <label class=" form-control-label" style="margin-left:1em;padding-top:0.2em">Quantity :</label>
                        <input type='number' class="form-control" style="width:10em;margin-left:1em" v-model="quantity" @change="cal_num" required>                   
                </div>
                <div class="row" style="margin-left:3em">
                        <label class=" form-control-label" style="margin-left:1em;padding-top:0.2em">Total Cost :</label>
                        <input type='number' step='0.001' class="form-control" style="width:10em;margin-left:1em" v-model="total" readonly>                   
                </div>               
              </div>     
              <br/>
              <div style='display:flex;flex-direction:row;width:100%'> <!--fund src-->
                <div class="row">
                        <label class=" form-control-label" style="padding-top:0.2em;margin-left:1em">Fund Source :</label>
                        <select class="form-control select" v-model="budget.fund[0]" style="width:15em;margin-left:1em">
                            <option v-for="fundsrc in fund_list">{{ fundsrc.name }}</option>
                        </select>               
                </div>
                <div class="row" style="margin-left:3em">
                        <label class=" form-control-label" style="margin-left:1em;padding-top:0.2em">Cost :</label>
                        <input type='number' step='0.001' min=0.001 class="form-control" style="width:10em;margin-left:1em" v-model="fund_cost[0]" required>   
                        <div class="fa fa-plus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="new_row"/>                 
                </div>                 
                </div>
                <div v-for="n in counter">
                    <div style='display:flex;flex-direction:row;width:100%;margin-top:0.4em'> <!--fund src extra-->
                        <div class="row">
                            <label class=" form-control-label" style="padding-top:0.2em;margin-left:1em">Fund Source :</label>
                            <select class="form-control select" v-model="budget.fund[n]" style="width:15em;margin-left:1em">
                                <option v-for="fundsrc in fund_list">{{ fundsrc.name }}</option>
                            </select>               
                        </div>
                    <div class="row" style="margin-left:3em">
                        <label class=" form-control-label" style="margin-left:1em;padding-top:0.2em">Cost :</label>
                        <input type='number' step='0.001' class="form-control" min=0.001 style="width:10em;margin-left:1em" v-model="fund_cost[n]" required>   
                        <div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row"/>                 
                </div>                 
                </div>  
            </div>
            <div v-html="message">{{message}}</div>
            </div>     

        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm" onclick="document.getElementById('general_req_form').reset()">
              <i class="fa fa-ban"></i> Reset
            </button>
        </div>
      </form>
    </div>  
      <!--check form-->  
    </div>  
    </div>
</template>


<script>
import axios from 'axios';
export default {
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<3){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    await axios.get('Procurement/department/backend/dept_member.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.budget.dept = this.dept_list[0].department;
                this.dept_ready = true;
                console.log(this.dept_list)
            }
        );
    var formData = new FormData();
        formData.append('dept',this.budget.dept);
        await axios.post('Procurement/budget/backend/subdept.php',formData)
        .then(
            (response)=>{
                    this.sub_dept_list = response.data;                  
                    if(this.sub_dept_list==''){
                        this.display_sub = false;
                        this.budget.sub='na';
                    } else {
                        this.budget.sub = this.sub_dept_list[0].subdept_full;
                        this.display_sub = true;
                    }
                }
        )  
        await axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.asset_list = response.data;
                    this.budget.cat = this.asset_list[0].name;
                }
            )
        await axios.post('Procurement/budget/backend/fund.php',formData)
            .then(
                (response)=>{
                    this.fund_list = response.data;
                    this.budget.fund[0] = this.fund_list[0].name;
                }
            )    
  },
    data(){          
        var tmr =document.getElementById('tmr_date').value;
        return{
            dept_list:[],
            sub_dept_list:[],
            asset_list:[],
            fund_list:[],
            display_sub:false,
            unit_cost:null,
            quantity:null,
            total: null,
            check_num:true,
            fund_cost:[],
            fund_chose:[],
            counter:0,
            message:'',
            user_check:'',
            type_list:['Regular','Special'],
            budget:{
                dept:'',
                sub:'',
                description:'',
                cat:'',
                fund:[], 
                type:'Regular',           
            }
        }  
    },
    methods:{
        load_supdept(event){
            var formData = new FormData();
            formData.append('dept',this.budget.dept);
            axios.post('Procurement/budget/backend/subdept.php',formData)
            .then(
                (response)=>{
                    this.sub_dept_list = response.data;                  
                    if(this.sub_dept_list==''){
                        this.display_sub = false;
                        this.budget.sub = 'na';
                    } else {
                        this.budget.sub = this.sub_dept_list[0].subdept_full;
                        this.display_sub = true;
                    }
                }
            )
            axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.asset_list = response.data;
                    this.budget.cat = this.asset_list[0].name;
                }
            )
            axios.post('Procurement/budget/backend/fund.php',formData)
            .then(
                (response)=>{
                    this.fund_list = response.data;
                    this.budget.fund[0] = this.fund_list[0].name;
                }
            )
        },
        cal_num(event){
            this.total = this.unit_cost * this.quantity;
            if(isNaN(this.total)){
                this.total = '...';
                this.check_num = false;
            }
            this.fund_cost[0] = this.total;
            },

        new_row(event){
            this.counter = this.counter+1;
            this.fund_cost[0] = '';
            this.budget.fund[this.counter] = this.fund_list[0].name;
        },
        remove_row(event){
            this.counter = this.counter-1;
            if(this.counter ==0){
               this.total = this.unit_cost * this.quantity;
               if(isNaN(this.total)){
                this.total = '...';
                this.check_num = false;
            }
            this.fund_cost[0] = this.total;
            }
        },
        handleSubmit(event){
            var formData = new FormData();
            formData.append('dept',this.budget.dept);
            formData.append('sub_dept',this.budget.sub);
            formData.append('deptasset',this.budget.cat);
            formData.append('descript',this.budget.descript);
            formData.append('unit',this.unit_cost);
            formData.append('quant',this.quantity);
            formData.append('total',this.total);
            formData.append('deptfunds',this.budget.fund);
            formData.append('fund_value',this.fund_cost);
            formData.append('type',this.budget.type);
            axios.post('Procurement/budget/backend/insert_new_plan_sp.php',formData)
            .then(
                (response)=>{
                    this.message = response.data;
                    if(this.message=="<p><h4>Successfully Inserted Record ! Click <a href='#' onclick = 'exit_form()' style='color:rgb(238,28,37)'> HERE</a> to close </h4></p>"){
                    this.$router.push('view_budget');
                    }
                }
            )
        }
        },
        
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


